import React, { Component } from 'react';
import './App.css';
import { Layout, Menu, Input, AutoComplete, Select, Card, Button, Icon, notification} from 'antd';
import { isNullOrUndefined } from 'util';
import { post } from 'axios';

const Option = AutoComplete.Option;
const { Header, Content, Footer } = Layout;

const HOST = 'http://quizapi.stumagz.com:8082/api/register';

const quiz_api_config = {
  headers: { 'content-type': 'application/json' },
  withCredentials: false
};


class App extends Component {
  
  componentWillMount () {
    this.setState ({
      email: "",
      emails: [],
      ref:"",
      name:"",
      mobile:"",
      city:"",
      college:"",
      loading:false,
      responseId:null
    });
  }

  handleSubmit(){
    
    let data = {
      fullname: this.state.name,
      mobile: this.state.mobile,
      email: this.state.email,
      college: this.state.college,
      city: this.state.city,
      ref: this.state.ref,
      status: 0
    };

    if (!/[a-zA-Z][^#&<>"~;$^%{}?]{1,50}$/g.test(data.fullname)){
      notification.open({
        message: 'Incomplete',
        description: "Kindly enter a valid name"
      });

      return false;
    }

    if (!/^[a-zA-Z0-9.!#$%&'*+/=?^_`{|}~-]+@[a-zA-Z0-9](?:[a-zA-Z0-9-]{0,61}[a-zA-Z0-9])?(?:\.[a-zA-Z0-9](?:[a-zA-Z0-9-]{0,61}[a-zA-Z0-9])?)*$/igm.test(data.email)) {
      notification.open({
        message: 'Incomplete',
        description: "Enter a valid e-mail address"
      });

      return false;
    }

    if (!/^[6-9][0-9]{9}$/g.test(data.mobile)) {
      notification.open({
        message: 'Incomplete',
        description: "Enter a valid 10 digit mobile number"
      });

      return false;
    }

    if (!/[a-zA-Z][^#&<>"~;$^%{}?]{1,150}$/g.test(data.college)) {
      notification.open({
        message: 'Incomplete',
        description: "Enter Valid College Name"
      });

      return false;
    }

    if (!/[a-zA-Z][^#&<>"~;$^%{}?]{1,150}$/g.test(data.city)) {
      notification.open({
        message: 'Incomplete',
        description: "Enter a valid city name"
      });

      return false;
    }

    if (data.ref === "") {
      notification.open({
        message: 'Incomplete',
        description: "Enter How you know about UGS ??"
      });

      return false;
    }

    this.setState({ loading: true });
    post(HOST, JSON.stringify(data), quiz_api_config)
    .then(response => {
      this.setState({
        loading: false
      });
      // eslint-disable-next-line eqeqeq
      if(response.data.code == 200 && !isNaN(parseInt(response.data.message))) {
        this.setState({ responseId: response.data.message});
        localStorage.setItem("spotRegId", response.data.message);
      }
      else {
        notification.open({
          message: 'Something went wrong',
          description: response.data.message
        });
      }
    })
    .catch((error) => {
      this.setState({
        loading: false
      });
      notification.open({
        message: 'Something went wrong. Please try again later',
        description: 'Please check if you have already registered'
      });
      console.log("Iam in catch: " + error);
    });
  }

  handleSearch = (value) => {
    let emails;
    if (!value || value.indexOf('@') >= 0) {
      emails = [];
    } else {
      emails = ['gmail.com', 'live.com', 'stumagz.com', 'yahoo.co.in'].map(domain => `${value}@${domain}`);
    }
    this.setState({ emails });
  }

  componentDidMount() {
    this.setState({
      responseId: localStorage.getItem("spotRegId")
    });
  }

  getStateBasedCard = () => {
    if (!isNullOrUndefined(this.state.responseId))
      return (
        <Card>
          <h3>Your registraion number is <b>{this.state.responseId}</b></h3>
          <Button onClick={() => {this.setState({ responseId: null });localStorage.removeItem("spotRegId")}}><Icon type="left-circle" />Back</Button>
        </Card>
      )
    const { emails } = this.state;
    const emailChildren = emails.map(email => <Option key={email}>{email}</Option>);
    return (<Card title="Please submit below details to register on spot" bordered={false} style={{ alignItems: 'center', justifyContent: 'center', display: 'flex', flexDirection: 'column' }}>
      <div style={{ height: 400, display: 'flex', justifyContent: 'space-between', flexDirection: 'column' }}>
        <Input onChange={(value) => this.setState({ name: value.target.value })} style={{ width: 250 }} placeholder="Name" allowClear required />
        <AutoComplete
          style={{ width: 250 }}
          onSearch={this.handleSearch}
          placeholder="Email"
          allowClear
          required
          onChange={(value) => this.setState({ email: value })}
        >
          {emailChildren}
        </AutoComplete>
        <Input onChange={(value) => this.setState({ mobile: value.target.value })} style={{ width: 250 }} addonBefore="+91" placeholder="Mobile" allowClear required />
        <Input onChange={(value) => this.setState({ college: value.target.value })} style={{ width: 250 }} placeholder="College / Organisation" allowClear required />
        <AutoComplete filterOption={true} onChange={(value) => this.setState({ city: value })} style={{ width: 250 }} placeholder="City" allowClear required dataSource={["Hyderabad", "Vijayawada", "Rangareddy", "Vishakapatnam", "Nalagonda", "Mahabubnagar"]}/>
        <Select
          style={{ width: 250 }}
          placeholder="How do you know about UGS?"
          onChange={(value) => this.setState({ ref: value })}
        >
          <Option value="roadtougs">Road to UGS</Option>
          <Option value="social">Social Media</Option>
          <Option value="college">College Administration</Option>
          <Option value="volunteers">Volunteers</Option>
          <Option value="other">Other</Option>
        </Select><br />
        <Button onClick={() => this.handleSubmit()} loading={this.state.loading}>
          Submit
                </Button>
      </div>
    </Card>)
  }

  render() {
    return (
      <div className="App">
        <Layout className="layout">
          <Header style={{backgroundColor: "#000"}}>
            <img alt="logo" src={require("./undergrad-WHITE.png")} style={{ maxHeight: 60 }} /><span style={{color:"#fff"}}>Registration Desk</span>
          </Header>
          <Content style={{ padding: '0 50px', minHeight: "90vh",alignItems:'center',justifyContent:'center',flex:1,display:'flex', flexDirection:'column', backgroundColor: "#cb202d" }}>
            <p style={{ fontSize: "15px", padding: "5px", color: "#fff", margin: "10px" }}> Participate in Undergrad special quiz and win exciting prizes.<br /><a style={{ color: "#fff" }} href="//bit.ly/stumagzappone"><em style={{textDecoration:"underline"}}>Click here to download app now!</em>ðŸ‘ˆðŸ“±</a></p>
            {this.getStateBasedCard()}
          </Content>
          <Footer style={{ textAlign: 'center', color:"#fff", backgroundColor: "#cb202d" }}>
            Undergrad Summit Â©{(new Date()).getFullYear()}
        </Footer>
        </Layout>
      </div>
    );
  }
}

export default App;
